import Vue from 'vue'
import Router from 'vue-router'
import ListName from '../pages/listName.vue'
import AddPeople from '../pages/addPeople.vue'
import SearchName from '../pages/searchName.vue'
import Me from '../pages/me.vue'
import PeopleForm from '../pages/peopleForm.vue'
import Main from '../pages/main.vue'
import Login from '../pages/login.vue'
import Register from '../pages/register.vue'
import ChangePWD from '../pages/changePWD.vue'
import OpenAddPeople from '../pages/openAddPeople.vue'
import OpenAddVisit from '../pages/openAddVisit.vue'

import FirstPage from '../pages/firstPage.vue'
import Success from '../pages/success.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path:"/",
      redirect:"/main"
    },
    {
      path: "/main",  // 
      component: Main,
      redirect:"/main/listName",
      children:[
        {
          path: '/main/listName', component: ListName  //
        }, {
          path: '/main/addPeople', component: AddPeople  // 
        }, {
          path: '/main/searchName', component: SearchName // 
        }, {
          path: '/main/me', component: Me // 
        },
      ],
      
    },
    {
      path: '/peopleForm', component: PeopleForm //
    },
    {
      path: '/login', component: Login // 
    },
    {
      path: '/register', component: Register  // 
    },
    {
      path: '/changePWD', component: ChangePWD  // 
    },
    {
      path: '/openAddPeople', component: OpenAddPeople // 
    },
    {
      path: '/openAddVisit', component: OpenAddVisit  // 
    },
    {
      path: '/firstPage', component: FirstPage  // 
    },
    {
      path: '/success', component: Success // 
    }

  ]
})
