<template>
  <div id="index">
    <mt-header title="" fixed>
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <div class="content-div">
      <ul>
        <li>
          <span> {{data.add_time}}</span>
        </li>
      </ul>
    </div>
    <div class="text-header"></div>
    <ul>
      <li>
        <span>: {{data.name}}</span>
        <span>: {{data.idcard}}</span>
      </li>
      <li>
        <!-- <span>: {{data.carid}}</span> -->
        <span>: {{data.phone}}</span>
      </li>
      <li>
        <span>: {{data.is_local?"":""}}</span>
        <span>: {{data.is_fromhubei?"":""}}</span>
      </li>
      <li>
        <span>: {{data.comefrom}}</span>
      </li>
      <li>
        <span>: {{data.desc==""?"":data.desc}}</span>
        <span>: {{data.travel_mode==1?"":(data.travel_mode==2?"":(data.travel_mode==3?"":(data.travel_mode==4?"":"")))}}</span>
      </li>
      <li>
        <span>//: {{data.carid==""?"":data.carid}}</span>
      </li>
      <li>
        <span>: {{data.springtime}}</span>
      </li>
    </ul>

    <div v-for="(item,i) in visitinfo" :key="i">
      <div class="text-header"></div>
      <ul>
        <li>
          <span>: {{item.contactname}}</span>
          <span>: {{item.contactphone}}</span>
        </li>
        <li>
          <span>: {{item.reason}}</span>
        </li>
        <li>
          <span>: {{item.temperature}}</span>
          <span>: {{item.add_time}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { peopleInfo } from "@/service/api/peopleInfo";
export default {
  data() {
    return {
      data: {},
      visitinfo:[],
    };
  },
  methods: {
    async getPeopleInfo() {
      let data = await peopleInfo({id:this.$route.query.id,userid:JSON.parse(localStorage.getItem("uinfo")).id});
      //let data = await peopleInfo({id:this.$route.query.id,userid:"1"});
      this.data = data || {};
      this.visitinfo = this.data.visitinfo || [];
    }
  },
  mounted() {
    this.getPeopleInfo();
  }
};
</script>
<style scoped>
#index {
  margin-bottom: 15px;
}
.content-div {
  margin-top: 45px;
}
.text-header {
  margin-top: 10px;
  margin-left: 5px;
  font-size: 18px;
  line-height: 40px;
  border-bottom: 1px solid #cccccc;
}
li {
  margin-left: 10px;
  margin-top: 10px;
}
span {
  border-radius: 8px;
  background-color: #cccccc;
  margin-right: 8px;
  display: inline-block;
  padding: 5px;
  font-size: 12px;
}
</style>